import React, { Component } from 'react'
import styled from '@emotion/styled'
import { css, keyframes } from '@emotion/core'
import { Button, Menu, Dropdown, Icon } from 'antd'
import { Container, Row, Col } from 'reus'
import { LangContextConsumer } from 'Components/Context'
import { GlobalnavContextProvider, GlobalnavContextConsumer } from 'Components/Context/GlobalnavContext'
import { FormattedMessage } from 'react-intl'
import { Link } from 'gatsby'
import logo from 'Images/logo-modana.png'

class Globalnav extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isVisible: false
    }
  }
  render() {
    const showMenu = keyframes`
      0% {
        opacity: 0;
        transform: translateY(-20px);
      }
      100% {
        opacity: 1;
        transform: translateY(0);
      }
    `
    const Nav = styled.nav`
      height: ${({toggle}) => toggle ? '100vh' : '60px'};
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      z-index: 999999;
      width: 100%;
      box-shadow: 0 2px 8px 0 rgba(198, 214, 239, 0.4);
      background-color: #ffffff;
      transition: background .36s cubic-bezier(0.32, 0.08, 0.24, 1),height .56s cubic-bezier(0.52, 0.16, 0.24, 1);
      `
    const Flex = styled.div`
      display: flex;
      ${({gutter, align}) => {
        return css`
          margin-left: -${gutter}px;
          margin-right: -${gutter}px;
          & > * {
            padding: 0 ${gutter}px;
          }
          align-items: ${align};
        `
      }}
    `
    const ButtonLogin = styled(Button)`
      color: #fff;
      height: 32px;
      border-radius: 16px;
      min-width: 71.38px;
      background-color: ${props => props.theme.color.secondary};
      border: 1px solid ${props => props.theme.color.secondary};
    `
    const ButtonSignUp = styled(Button)`
      color: ${props => props.theme.color.primary};
      min-width: 106.89px;
      height: 32px;
      border-radius: 16px;
      border: 1px solid ${props => props.theme.color.secondary};
    `

    const services = (
      <Menu css={css`
        border-radius: 14px;
        min-width: 220px;
        padding: 10px 0;
        box-shadow: 0 8px 25px #33333312;
        a {
          padding: 7px 18px;
        }
      `}>
        <Menu.Item key="0">
          <Link to="/app/overview">HR</Link>
        </Menu.Item>
        <Menu.Item key="1">
          <Link to="/payroll">Payroll</Link>
        </Menu.Item>
        <Menu.Item key="2">
          <Link to="/lending">Lending</Link>
        </Menu.Item>
      </Menu>
    )
    const MenuLink = styled.a`
      color: ${({theme}) => theme.color.primary};
      position: relative;
      font-weight: bold;
      @media screen and (max-width: ${({theme}) => theme.breakPoints.md}) {
        border-bottom: 1px solid #d8d8d8;
        padding: 12px 0;
        display: block;
      }
      & ~ div .ant-dropdown {
        @media screen and (min-width: ${({theme}) => theme.breakPoints.md}) {
          top: 73.5px !important;
        }
      }
    `
    const StyledLink = styled(Link)`
      color: ${({theme}) => theme.color.primary};
      @media screen and (max-width: ${({theme}) => theme.breakPoints.md}) {
        border-bottom: 1px solid #d8d8d8;
        padding: 12px 0;
        display: block;
      }
      font-weight: bold;
    `
    const ToggleStyled = styled(Button)`
      border-radius: 21px;
    `
    const StyledLang = styled(Button)`
      border-radius: 21px;
      border: none;
      text-transform: uppercase;
      box-shadow: none;
      border: 1px solid #eaeaea;
      width: 49px;
    `
    const GlobalnavPlaceholder = styled.div`
      height: 60px;
    `
    const CircleIcon = styled(Icon)`
      vertical-align: middle;
      display: none;
      @media screen and (max-width: ${({theme}) => theme.breakPoints.md}) {
        right: 0;
        display: inline-block;
        top: 20px;
        position: absolute;
        color: #909090;
      }
    `
    const StyledCol = styled(Col)`
      margin-top: 70px;
      @media screen and (min-width: ${({theme}) => theme.breakPoints.md}) {
        display: none;
      }
    `
    const LangMobile = styled(Button)`
      width: 100%;
      height: 42px;
      color: #fff;
      border-radius: 21px;
      margin-bottom: 20px;
      border: 1px solid #eaeaea;
      color: #444;
      font-weight: bold;
      text-transform: uppercase;
    `
    const SignInMobile = styled(Button)`
      width: 100%;
      height: 42px;
      color: #fff;
      border-radius: 21px;
      margin-bottom: 20px;
      color: ${({theme}) => theme.color.primary};
      border: 1px solid ${({theme}) => theme.color.secondary};
      font-weight: bold;
      text-transform: uppercase;
    `
    const GetStartedMobile = styled(Button)`
      width: 100%;
      height: 42px;
      border-radius: 21px;
      background: ${({theme}) => theme.color.secondary};
      color: #fff;
      font-weight: bold;
      text-transform: uppercase;
    `
    
    return (
      <GlobalnavContextProvider>
        <GlobalnavContextConsumer>
          {
            ({toggle, handleToggle}) => (
              <>
                <Nav toggle={toggle}>
                  <Container xl gutter={22}>
                    <Row justify="space-between" align="center" gutter={16} css={css`
                      height: 60px;`}>
                      <Col>
                        <a href="#"><img css={css`height: 40px;`} src={logo} alt=""/></a>
                      </Col>
                      <Col>
                        <Row gutter={25} align="center">
                          <Col css={theme => {
                              return css`
                                @media screen and (max-width: ${theme.breakPoints.md}) {
                                  display: ${toggle ? 'block' : 'none'};
                                  position: absolute;
                                  top: 90px;
                                  left: 0;
                                  width: 100%;
                                }
                              `
                            } 
                          }>
                            <Row gutter={20} css={theme => {
                                return css`
                                  @media screen and (max-width: ${theme.breakPoints.md}) {
                                    opacity: 0;
                                    transform: translateY(-20px);
                                    animation: ${showMenu} 1s ease .6s both;
                                    display: block !important;
                                    & > div > a {
                                      font-size: 28px;
                                      & ~ div {
                                        position: relative !important;
                                        & > div > div {
                                          position: relative !important;
                                          left: 0 !important;
                                          top: 0 !important;
                                          & > ul {
                                            margin: 20px 0;
                                          }
                                        }
                                      }
                                    }
                                  }
                                `
                              } 
                            }>
                              <Col>
                                <Dropdown 
                                  overlay={services} 
                                  getPopupContainer={(node) => node.parentElement}
                                  trigger={['click']}
                                  visible={this.state.isVisible}
                                  onVisibleChange={visible => this.setState({isVisible: visible})}
                                  placement="bottomCenter">
                                  <MenuLink>
                                    <FormattedMessage id="service"/>
                                    <Icon css={theme => css`
                                      vertical-align: middle;
                                      @media screen and (max-width: ${theme.breakPoints.md}) {
                                        display: none !important;
                                      }
                                    `} type="down" />
                                    {
                                      this.state.isVisible ? 
                                        <CircleIcon type="minus-circle"/> : <CircleIcon type="plus-circle"/>
                                    }
                                  </MenuLink>
                                </Dropdown>
                              </Col>
                              <Col>
                                <StyledLink to="/app/about-us"><FormattedMessage id="aboutUs"/></StyledLink>
                              </Col>
                              <Col>
                                <StyledLink to="/app/help-center"><FormattedMessage id="helpCenter"/></StyledLink>
                              </Col>
                              <StyledCol>
                                <div>
                                  <LangContextConsumer>
                                  {
                                    ({lang, handleLang}) => (
                                      <LangMobile onClick={handleLang}>
                                        {lang === 'en' ? 'Indonesia' : 'English'}
                                      </LangMobile>
                                    )
                                  }
                                </LangContextConsumer>
                                </div>
                                <div>
                                  <SignInMobile>Sign In</SignInMobile>
                                </div>
                                <div>
                                  <GetStartedMobile>Get Started</GetStartedMobile>
                                </div>
                              </StyledCol>
                            </Row>
                          </Col>
                          <Col>
                            <Row gutter={10} align="center">
                              <Col css={theme => {
                                  return css`
                                    @media screen and (max-width: ${theme.breakPoints.sm}) {
                                      display: none;
                                    }
                                  `
                                } 
                              }>
                                <LangContextConsumer>
                                  {
                                    ({lang, handleLang}) => (
                                      <StyledLang onClick={handleLang}>{lang === 'en' ? 'id' : 'en'}</StyledLang>
                                    )
                                  }
                                </LangContextConsumer>
                              </Col>
                              <Col>
                                <ButtonSignUp><FormattedMessage id="getStarted"/></ButtonSignUp>
                              </Col>
                              <Col css={theme => {
                                  return css`
                                    @media screen and (max-width: ${theme.breakPoints.sm}) {
                                      display: none;
                                    }
                                  `
                                } 
                              }>
                                <ButtonLogin><FormattedMessage id="login"/></ButtonLogin>
                              </Col>
                              <Col css={theme => {
                                  return css`
                                    @media screen and (min-width: ${theme.breakPoints.md}) {
                                      display: none;
                                    }
                                  `
                                } 
                              }>
                                <ToggleStyled onClick={handleToggle}>
                                  <Icon css={css`transform: rotate(90deg);`} type="pause" />
                                </ToggleStyled>
                              </Col>
                            </Row>
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                  </Container>
                </Nav>
                <GlobalnavPlaceholder/>
              </>
            )
          }
        </GlobalnavContextConsumer>
      </GlobalnavContextProvider>
    )
  }
}

export default Globalnav